.no-p-margin {
    p {
        margin-bottom: 0;
    }
}

.plenary-speaker-floater {
    width: 100%;
    float: left;
    margin-bottom: 50px;
}

.sessional-speaker-floater {
    width: 50%; 
    float: left;
    margin-bottom: 50px;
}

.speakersession-title {
    text-align: center;
    margin-bottom:30px;
}

.profile-pic-gallary {
    padding: 0px;
    display: inline;
}

.plenary-image--cover-container {
    width: 20%;
    float: left;
    text-align: center;
    margin: 0px;
    padding: 0px;
}

.image--cover-container {
    width: 33%;
    float: left;
    text-align: center;
    margin: 0px;
    padding: 0px;
    // margin-right: 40px;
}

.plenary-image--cover {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-bottom: 10px;

    object-fit: cover;
    object-position: center;
}

.image--cover {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin-bottom: 10px;

    object-fit: cover;
    object-position: center;
}

@media only screen and (max-width: 800px) {
    .plenary-image--cover-container {
        width: 50%;
    }
    .sessional-speaker-floater {
        width: 100%;
    }
}

.announcement-box {
    padding: 15px;
    margin-bottom: 20px;
    border: 1px solid transparent;
    border-radius: 4px;
    background-color: #fcf8e3;
    border-color: #faebcc;
}

.center-cropped {
    width: 100px;
    height: 60px;
    background-repeat: no-repeat;
    overflow: hidden;
    border: 2px solid $primary-color;
    border-radius: 5%;
    margin-right: 10px;
    background-position: center;
    background-size: contain;
  }
  
  /* Set the image to fill its parent and make transparent */
  .center-cropped img {
    min-height: 100%;
    min-width: 100%;
    /* IE 8 */
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    /* IE 5-7 */
    filter: alpha(opacity=0);
    /* modern browsers */
    opacity: 0;
  }

  .lecture-container {
  
    display: flex;
    width: 100%;
    padding-top: 10px;
    padding-bottom: 10px;
    
    .thumbnail {
      width: 120px;
    }
    
    .content {
      flex: 1;
    }
  }

  .previous-offerings{
    list-style-type: none;
    

    li {
        display: inline-block;
        margin-right: 10px; 
    }

    li::before {
        content: "● ";
        color:black;
        padding-right: 5px;
    } 
  }

  .lecture-links {
      font-weight: bold;
      font-family: 'Titillium Web', sans-serif;
  }

  .schedule-lecture-links {
    font-weight: normal;
    font-family: inherit;
  }

.markdown-content {
    p,ol {
        margin-bottom: 3px;
    }

    li {
        padding: 0px;
        display: list-item !important;
        border-bottom: 0 !important;
    }
}